/*! Preset v0.6.0 | MIT License | https://github.com/Microflash/preset */
*,
::before,
::after {
  box-sizing: border-box;
}

:where(html) {
  text-size-adjust: 100%;
  block-size: 100%;
}
@media (prefers-reduced-motion: no-preference) {
  :where(html):focus-within {
    scroll-behavior: smooth;
  }
}

:where(body) {
  min-block-size: 100%;
  tab-size: 2;
}

* {
  margin: 0;
  line-height: calc(0.3rem + 2ex + 0.3rem);
  overflow-wrap: break-word;
}

:where(a, u, ins, abbr) {
  text-underline-offset: 1px;
}
@supports (-moz-appearance: none) {
  :where(a, u, ins, abbr) {
    text-underline-offset: 2px;
  }
}

:where(:not(fieldset, progress, meter)) {
  background-origin: border-box;
  background-repeat: no-repeat;
}

:where(abbr[title]) {
  text-decoration-line: underline;
  text-decoration-style: dotted;
  cursor: help;
}

:where(ul, ol)[role=list] {
  list-style: none;
}

:where(img, svg, canvas, audio, video, iframe, embed, object) {
  display: block;
  max-inline-size: 100%;
}

:where(img, svg, video) {
  block-size: auto;
}

:where(svg) {
  stroke-linecap: round;
  stroke-linejoin: round;
}

:where(iframe) {
  border: none;
}

:where(input, button, textarea, select, optgroup),
:where(input[type=file])::-webkit-file-upload-button {
  color: inherit;
  font: inherit;
  font-size: inherit;
  letter-spacing: inherit;
  word-spacing: inherit;
}

:where(textarea) {
  resize: vertical;
}
@supports (resize: block) {
  :where(textarea) {
    resize: block;
  }
}

:where(a[href], area, button, input, label[for], select, summary, textarea, [tabindex]:not([tabindex*="-"])) {
  cursor: pointer;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

:where(input[type=file]) {
  cursor: auto;
}
:where(input[type=file])::-webkit-file-upload-button, :where(input[type=file])::file-selector-button {
  cursor: pointer;
}

:where(button, button[type], input[type=button], input[type=submit], input[type=reset]),
:where(input[type=file]):is(::-webkit-file-upload-button, ::file-selector-button) {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  user-select: none;
}

:where(button, button[type], input[type=button], input[type=submit], input[type=reset])[disabled] {
  cursor: not-allowed;
}

:where(table) {
  border-collapse: collapse;
  border-spacing: 0;
}

@media (prefers-reduced-motion: no-preference) {
  :where(:focus-visible) {
    transition: outline-offset 145ms cubic-bezier(0.25, 0, 0.4, 1);
  }
  :where(:not(:active):focus-visible) {
    transition-duration: 0.25s;
  }
}
